.container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    list-style: none;
    background-color: #140f07cc;
    padding-bottom: 10px;
    border-bottom: 1px solid #7b7dda;
}

.pageButtons{
    background-color: #1a2540;
    padding: 10px 23px;
    margin: 0px 1px;
    border-radius: 2px;
    color: white;
    border: 1px solid #7b7dda;
}

.pageButtons:hover{
    background-color: #140f07;
    cursor: pointer;
}

.pageButtons:active{
    background-color: #140f07;
}

.pageButtons:focus{
    background-color: #140f07;
}