.loading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    color: white;
    font-size: larger;
    font-weight: bold;
}

.loading img{
    width: 250px;
    border-radius: 10px;
    margin-bottom: 30px;
}